function App() {
  return (
<div> <CS /> </div>
  );
}

function AptLvl(item, pc) {
  var apts = 0;
  if( Aptitudes[item.apt_1] & pc.apt ) {
    apts++;
  }
  if( Aptitudes[item.apt_2] & pc.apt ) {
    apts++;
  }
  return apts;
}

function SkillValue(item, pc) {

  return apt_skill_cost[AptLvl(item, pc).toString()];
}

function StatValues(item, pc) {
  return apt_stat_cost[AptLvl(item, pc).toString()];
}

class CS extends React.Component {

  constructor( props ) {
    super(props);
    this.UpdatePC = this.UpdatePC.bind(this);
    this.GetSkills = this.GetSkills.bind(this);
    this.ModifySearchTerm = this.ModifySearchTerm.bind(this);
    this.ChangeSkill = this.ChangeSkill.bind(this);
    this.CleanSearchTerm = this.CleanSearchTerm.bind(this);
    this.ChangeStat = this.ChangeStat.bind(this);
    Skills.map((skill) => {
      pc.skills.set(skill.name, [0, 0, 0, 0])
    })
    Stats.map((stat) => {
      pc.stats.set(stat.name, [0, 0, 0, 0, 0]);
    })
    this.state = { pc: pc, searchTerm: "" }
  }

  UpdatePC(  ) {
    this.setState({pc: pc});
  }

  ModifySearchTerm(onChangeEvent) {
    const target = onChangeEvent.target;
    this.setState({pc: pc, searchTerm: target.value});
  }

  CleanSearchTerm() {
    this.ModifySearchTerm({target:{value: ''}});
  }

  GetSkills(  ){
    return Skills.filter(skill => {
        return (
            skill.name.toUpperCase().startsWith(this.state.searchTerm.toUpperCase()) ||
            skill.apt_1.toUpperCase().startsWith(this.state.searchTerm.toUpperCase()) ||
            skill.apt_2.toUpperCase().startsWith(this.state.searchTerm.toUpperCase())
        );
    });
  }

  ChangeSkill(SkillName, SkillLevel) {
    let skillRanks = pc.skills.get(SkillName);
    skillRanks[SkillLevel] = (skillRanks[SkillLevel]) ? 0 : 1;
    pc.skills.set(SkillName, skillRanks);
    this.setState({pc:pc, searchTerm: this.state.searchTerm ?? ""});
  }

  ChangeStat(StatName, StatLevel) {
    let statRanks = pc.stats.get(StatName);
    statRanks[StatLevel] = (statRanks[StatLevel]) ? 0 : 1;
    pc.stats.set(StatName, statRanks);
    this.setState({pc: pc, searchTerm: this.state.searchTerm});
  }

  render( ) {
    const skills = this.GetSkills().map((i) => { return <SkillItem item={i} change={this.ChangeSkill} /> });
    const stats = Stats.map((stat) =>
      { return ( <StatItem item={stat} change={this.ChangeStat} /> ) });
    return (
      <div className="cs">
        <div className="PC-BAR">
          <AptitudesBar update={this.UpdatePC}/>
        </div>
        <div className="flex-column second-column">
          <div className="skills">
            <div className="Search-Bar">
              <input type="text" onChange={this.ModifySearchTerm} value={this.state.searchTerm} />
              <button onClick={this.CleanSearchTerm}>X</button>
            </div>
            <table className="tt">
              <thead>
                <tr>
                  <th> Skill </th>
                  <th> Aptitude 1 </th>
                  <th> Aptitude 2 </th>
                  <th> +0  </th>
                  <th> +10 </th>
                  <th> +20 </th>
                  <th> +30 </th>
                </tr>
              </thead>
              <tbody>
                {skills}
              </tbody>
            </table>
          </div>
          <div className="stats">
          <table className="tt">
            <thead>
              <tr>
                <th> Characteristic </th>
                <th> Aptitude 1 </th>
                <th> Aptitude 2 </th>
                <th> +5 </th>
                <th> +10 </th>
                <th> +15 </th>
                <th> +20 </th>
                <th> +25 </th>
              </tr>
            </thead>
            <tbody>
            {stats}
            </tbody>
          </table>
          </div>
        </div>
        <div className="flex-column third-column" >
          <ExpTotal />
        </div>
        </div>
    );
  }
}

class SkillItem extends React.Component {

  constructor( props ) {
    super( props );
    this.Change = this.Change.bind(this);
    this.Activated = this.Activated.bind(this);
  }

  Activated( lvl ) {
    return  (pc.skills.get(this.props.item.name)[lvl]);
  }

  Change(lvl) {
    this.props.change(this.props.item.name, lvl)
  }

  render() {
    const values = SkillValue(this.props.item, pc);
    return <tr>
             <td> { this.props.item.name } </td>
             <td> { this.props.item.apt_1 } </td>
             <td> { this.props.item.apt_2 } </td>
             <td onClick={()=>{ return this.Change(0)}} className={this.Activated(0) ? "bought" : ""}> { values[0] } </td>
             <td onClick={()=>{ return this.Change(1)}} className={this.Activated(1) ? "bought" : ""}> { values[1] } </td>
             <td onClick={()=>{ return this.Change(2)}} className={this.Activated(2) ? "bought" : ""}> { values[2] } </td>
             <td onClick={()=>{ return this.Change(3)}} className={this.Activated(3) ? "bought" : ""}> { values[3] } </td>
           </tr>;
  }
}

