class AptitudesBar extends React.Component{
  apts = [
    'Agility',
    'Ballistic Skill',
    'Defense',
    'Fellowship',
    'Fieldcraft',
    'Finesse',
    'General',
    'Intelligence',
    'Knowledge',
    'Leadership',
    'Offence',
    'Perception',
    'Psyker',
    'Social',
    'Strength',
    'Tech',
    'Toughness',
    'Weapon Skill',
    'Willpower',
  ]
  render () {
    const checks = this.apts.map((apt) => {
              return <AptitudeCheck apt={apt} callback={this.props.update}/>;
            });
    return <div className="Aptitude-bar">
             <h2> Aptitudes </h2>
            {checks}
          </div>
    ;
  }
}

class AptitudeCheck extends React.Component {

  constructor( props ) {
    super( props );
    this.state = { aptName: props.apt, active: (Aptitudes[props.apt] & pc.apt) ? true : false };
    this.handleChange = this.handleChange.bind(this);
    this.return = props.callback;
  }

  handleChange( event ) {
    if(event.target.checked) {
      pc.apt = pc.apt | Aptitudes[this.state.aptName];
    }
    else {
      pc.apt = pc.apt & ~Aptitudes[this.state.aptName];
    }
    this.setState( { aptName: this.state.aptName, active: (Aptitudes[this.state.aptName] & pc.apt) ? true : false } );
    this.return();
  }

  render () {
    return <div> <CheckBox checked={this.state.active} onChange={this.handleChange} content={this.state.aptName} /> </div>
  }
}

class ExpTotal extends React.Component {

  constructor( props ) {
    super( props );
    this.expTotal = this.expTotal.bind(this);
  }

  expTotal() {
    let expTotal = 0;
    let expList = [];

    Skills.map((skill) => {
      let values = SkillValue(skill, pc);
      const ranks = pc.skills.get(skill.name);
      if( ranks[0] ){
        expTotal += values[0];
        expList.push(`${skill.name} +0 (${values[0]})`);
      }
      if( ranks[1] ) {
        expTotal += values[1];
        expList.push(`${skill.name} +10 (${values[1]})`);
      }
      if( ranks[2] ) {
        expTotal += values[2];
        expList.push(`${skill.name} +20 (${values[2]})`);
      }
      if( ranks[3] ) {
        expTotal += values[3];
        expList.push(`${skill.name} +30 (${values[3]})`);
      }

    })

    Stats.map( (stat) => {
      let values = StatValues(stat, pc);
      const ranks = pc.stats.get(stat.name);
      if( ranks[0] ) {
        expTotal += values[0];
        expList.push(`${stat.name} +5 (${values[0]})`);
      }
      if( ranks[1] ) {
        expTotal += values[1];
        expList.push(`${stat.name} +10 (${values[1]})`);
      }
      if( ranks[2] ) {
        expTotal += values[2];
        expList.push(`${stat.name} +15 (${values[2]})`);
      }
      if( ranks[3] ) {
        expTotal += values[3];
        expList.push(`${stat.name} +20 (${values[3]})`);
      }
      if( ranks[4] ) {
        expTotal += values[4];
        expList.push(`${stat.name} +25 (${values[4]})`);
      }
    } )

    return {short: expTotal, long: expList};
  }

  render() {
    const expTotals = this.expTotal();
    return (
      <div>
        <div>
          Exp Cost: {expTotals.short}
        </div>
        <ul className="item-list">
          {expTotals.long.map((item)=>{return(<li> {item} </li>)})}
        </ul>
      </div>
    );
  }
}

class CheckBox extends React.Component {
  constructor( props ) {
    super( props );
  }

  render() {
    return (
      <label className="container"> {this.props.content}
        <input type="checkbox" checked={this.props.checked} onChange={this.props.onChange}/>
        <span className="checkmark"></span>
      </label>
    );
  }
}

export default App;
